@font-face {
  font-family: 'PerfectDos';
  src: url("/fonts/Perfect-DOS-VGA-437.ttf") format("truetype"); }

* {
  font-family: 'PerfectDos';
  font-size: 20px;
  line-height: 1em; }

html {
  font-size: 16px; }

h1, h2, h3, h4, h5, h6, p {
  margin: 0.5em 0;
  color: white; }

h1 {
  display: inline;
  font-size: 1em;
  line-height: 1em; }

h2 {
  font-size: 1.2em; }

h3 {
  font-size: 1.1em; }

h4 {
  font-size: 1em; }

h5 {
  font-size: 1em; }

h6 {
  font-size: 1em; }

p {
  font-size: 1em;
  line-height: 1.2em; }

a {
  line-height: 1.2em; }

.center {
  text-align: center !important; }

* {
  box-sizing: border-box; }

body {
  background-color: black;
  background-size: cover;
  background-position: fixed; }
  @media only screen and (min-width: 768px) and (min-height: 0px) {
    body {
      overflow: hidden; } }

@media only screen and (min-width: 768px) and (min-height: 0px) {
  html, body {
    height: 100%; } }

body {
  padding: 20px; }

.links-container {
  display: flex;
  align-items: center;
  flex-direction: column; }
  @media only screen and (min-width: 768px) and (min-height: 0px) {
    .links-container {
      flex-direction: row;
      height: 100%; } }

.links {
  float: left;
  min-width: 45%;
  width: 500px;
  max-width: 100%;
  margin: 30px 2.5%; }
  .links ul {
    margin: 0;
    padding: 0;
    list-style: none; }
  .links li {
    margin: 20px 0; }
    .links li:first-child {
      margin-top: 0; }
    .links li:last-child {
      margin-bottom: 0; }
  .links a {
    display: block;
    padding: 0 10px;
    font-size: 20px;
    color: white;
    text-decoration: none;
    transform: translateX(0);
    transition: transform 0.3s; }
    .links a:hover, .links a:focus {
      outline: 0;
      background-color: white;
      color: black; }

.box {
  position: relative;
  padding: 10px;
  box-shadow: 5px 5px 0 0 black;
  background-color: rgba(0, 0, 0, 0.5);
  opacity: 0.5;
  transition: 0.1s;
  text-align: center; }
  @media only screen and (min-width: 768px) and (min-height: 0px) {
    .box {
      text-align: left; } }
  .box:hover, .box:focus {
    opacity: 1; }
  .box a {
    color: white; }

.box-title {
  position: absolute;
  top: 0;
  left: 50%;
  height: 31px;
  padding: 0 15px;
  color: white;
  transform: translateX(-50%) translateY(-50%);
  font-size: 24px;
  line-height: 30px;
  white-space: nowrap; }
  .box-title:before {
    z-index: -1;
    content: '';
    position: absolute;
    left: 0;
    width: 100%;
    height: 50%;
    background-color: rgba(0, 0, 0, 0.5);
    box-shadow: 7px 2px 0 -2.5px black; }

