button {
  background: transparent;
  border: none;
  color: white; }
  button:hover .tick span:before {
    content: 'x'; }
  button .tick {
    position: relative;
    float: left;
    display: block;
    height: 15px;
    padding: 2px 4px;
    margin-right: 10px; }
    button .tick:before, button .tick:after {
      position: absolute;
      top: 0;
      font-size: 20px; }
    button .tick:before {
      content: '[';
      left: -5px; }
    button .tick:after {
      content: ']';
      right: -4px; }
    button .tick span {
      display: block;
      width: 12px;
      text-align: center;
      line-height: 13px; }

.about-image {
  margin: 30px 2.5%;
  transition: 0.3s; }
  @media only screen and (min-width: 768px) and (min-height: 0px) {
    .about-image {
      margin-top: 0;
      margin-bottom: 0; } }
  .about-image.about-image--show {
    transform: translateY(-100%); }
  .about-image .box-title {
    cursor: pointer; }
